(function() {
  'use strict';

  angular
  .module('lmp')
  .factory('createOrderFactory', createOrderFactory);

  createOrderFactory.$inject = ['$http', '$log', '$rootScope'];

  function createOrderFactory($http, $log, $rootScope) {
    return { createOrder };

    function createOrder(product_id, variation_id) {
      let url = `${$rootScope.base_url}/lmp-api/v1/create-order?product_id=${product_id}`;
      if (variation_id) url = `${url}&variation_id=${variation_id}`;

      return $http.post(url)
      .then(res => res.data)
      .catch(err => $log.debug(err));
    }
  }
})();
