(function() {
  'use strict';

  angular
  .module('lmp')
  .component('order', {
    bindings: {
      data: '<'
    },
    controller: ['orderFactory', 'cancelSubFactory', '$timeout', 'authFactory', '$state', '$stateParams', '$localStorage', function(orderFactory, cancelSubFactory, $timeout, authFactory, $state, $stateParams, $localStorage) {
      this.cancel = () => {
        cancelSubFactory.cancel(this.data.order_id).then(res => $timeout(() => {
          zenscroll.to(document.body, 300);
          this.message = res.message;
          this.status = res.status;

          if (res.status) {
            orderFactory.getOrder(this.data.order_id).then(res => $timeout(() => {
              this.data = res;
            }));
          }
        }));
      }
    }],
    templateUrl: '/views/order.html'
  });
})();
