(function() {
  'use strict';

  angular
  .module('lmp')
  .component('layout', {
    bindings: {
      navigation: '<'
    },
    controller: ['$rootScope', '$window',  function($rootScope, $window) {
      $rootScope.layout = {
        mobile: $window.innerWidth < 961 || false,
        loading: true,
        fixed: false,
        sign_in: false
      };
    }],
    templateUrl: '/views/layout.html'
  });
})();
