(function() {
  'use strict';

  angular
  .module('lmp')
  .component('reviewOrder', {
    controller: ['authFactory', 'productFactory', '$stateParams', '$localStorage', '$location', function(authFactory, productFactory, $stateParams, $localStorage, $location) {
      zenscroll.to(document.body, 300);
      
      if (authFactory.isAuthenticated()) {
        this.auth = true;

        productFactory.getProduct($stateParams.product_id, $stateParams.variation_id).then(res => {
          this.data = res;
        });
      } else {
        $localStorage.login_redirect = $location.url();
      }
    }],
    templateUrl: '/views/review-order.html'
  });
})();
